import React, { Component } from 'react';
import axios from 'axios';
import {Accordion,Card,Button} from 'react-bootstrap'; 

class State extends Component {

    constructor(props)
    {
        super(props);
        this.state={
            statedata:{}
        }
    }
    componentDidMount()
    {
        axios.get("https://api.covid19india.org/state_district_wise.json").then(response=>{
            // console.log(response.data);
            this.setState({
                statedata:response.data
            
            })
        });
    }
    
    render() {
      let keys=Object.keys(this.state.statedata);
        return (
            <div className="row">
                <div className="col-md-12">
                <Accordion defaultActiveKey="0">
                  {
                    
                    keys.map((itm,indx)=>{
                      let district=this.state.statedata[itm].districtData;
                      let district_keys=Object.keys(district);
                      let total_active=0;
                      let total_confirmed=0;
                      let total_recovered=0;
                      let total_deaths=0;
                      let district_list=[];
                      for(let x in district)
                      {
                        total_active+=district[x].active;
                        total_confirmed+=district[x].confirmed;
                        total_recovered+=district[x].recovered;
                        total_deaths+=district[x].deceased;
                        let ob=district[x];
                        ob["district_name"]=x;
                        district_list.push(ob);  
                      }


                      return(<Card>
                        <Card.Header>
                          <Accordion.Toggle as={Button} variant="primary" eventKey={indx}>
                            {itm} <span className="btn-dark">Total cases {total_active}</span>
                          </Accordion.Toggle>
                        </Card.Header>
                        <Accordion.Collapse eventKey={indx}>
                          <Card.Body>
                            <table className="tavle table-bordered table-striped">
                              <thead>
                                  <tr>
                                      <td>District</td>
                                      <td>Confirmeed</td>
                                      <td>Active</td>
                                      <td>Active</td>
                                      <td>Death</td>
                                  </tr>
                              </thead>
                              <tbody>
                                {
                                
                                district_list.map((itm,index)=>{
                                  return(

                                    <tr>
                                        <td>{itm.district_name}</td>
                                        <td>{itm.confirmed}</td>
                                        <td>{itm.active}</td>
                                        <td>{itm.recovered}</td>
                                        <td>{itm.deceased}</td>
                                    </tr>
                                  )
                                })
                                
                                }
                              </tbody>
                            </table>
                            Hello! I'm the body</Card.Body>
                        </Accordion.Collapse>
                      </Card>)

                    })
                  }
  
  
</Accordion>

                </div>
                
                <p>{console.log(this.state.statedata)}</p>
            </div>
        );
    }
}

export default State;