import axios from 'axios';
import React, { Component } from 'react';

class World extends Component {
    constructor(){
        super();
        this.state={
            data:[]
        }
    }
    componentDidMount(){
        axios.get("https://corona.lmao.ninja/v2/countries").then(response=>{
            console.log(response.data)
            this.setState({
                data:response.data
            })
        })
    }
    render() {
        return (
            <div className="row">

                <div className="col-md-12">
                    <table className="tavle table-bordered table-striped">
                            <thead>
                                    <tr>
                                        <td>Country</td>
                                        <td>Country</td>
                                        <td>Total Cases</td>
                                        <td>Recovered</td>
                                        <td>Death</td>
                                    </tr>    
                            </thead> 
                            <tbody>
                                    {this.state.data.map((value,index)=>{
                                        return(<tr>
                                            <img src={value.countryInfo.flag} alt="" />
                                            <td>{value.country}</td>
                                            <td>{value.cases}</td>
                                            <td>{value.recovered}</td>
                                            <td>{value.deaths}</td>
                                        </tr>)
                                        
                                    })}    
                            </tbody>   
                    </table>  

                </div>
            </div>
        );
    }
}

export default World;