import React, { Component } from 'react';
import {Card} from 'react-bootstrap'
import State from './State';
import axios from 'axios';

class India extends Component {
  constructor(){
    super();
    this.state={
      data:{}
    }
  }
  componentDidMount(){
    axios.get("https://corona.lmao.ninja/v2/countries/india").then(response=>{
      this.setState({
        data:response.data
      })
    })
  }
    render() {
        return (
            <div className="row"> 
            <div className="col-md-64">
            <img src="https://www.countryflags.io/in/shiny/64.png" alt="" />
            <h3>India</h3>
            </div>
            <div className="col-md-12">
                <div className="row">
                    <div className="col-md-3">
                    <Card style={{ width: '18rem'}}>
  {/* <Card.Img variant="top" src="holder.js/100px180" /> */}
  <Card.Body className="text-center" style={{color:"yellow",backgroundColor:"blue"}}>
    <Card.Title>TOTAL CASES</Card.Title> 
    <h3>{this.state.data.cases}</h3>

    <Card.Text>
     [{this.state.data.todayCases}]
    </Card.Text>
    {/* <Button variant="primary">Go somewhere</Button> */}
  </Card.Body>
</Card>

                    </div>


                    <div className="col-md-3">
                    <Card style={{ width: '18rem'}}>
  {/* <Card.Img variant="top" src="holder.js/100px180" /> */}
  <Card.Body className="text-center" style={{color:"yellow",backgroundColor:"orange"}}>
    <Card.Title>ACTIVE CASES</Card.Title> 
    <h3>{this.state.data.active}</h3>

    <Card.Text>
     
    </Card.Text>
    {/* <Button variant="primary">Go somewhere</Button> */}
  </Card.Body>
</Card>

                    </div>
                    <div className="col-md-3">
                    <Card style={{ width: '18rem'}}>
  {/* <Card.Img variant="top" src="holder.js/100px180" /> */}
  <Card.Body className="text-center" style={{color:"yellow",backgroundColor:"green"}}>
    <Card.Title>RECOVERED</Card.Title> 
    <h3>{this.state.data.recovered}</h3>

    <Card.Text>
     
    </Card.Text>
    {/* <Button variant="primary">Go somewhere</Button> */}
  </Card.Body>
</Card>

                    </div>
                    <div className="col-md-3">
                    <Card style={{ width: '18rem'}}>
  {/* <Card.Img variant="top" src="holder.js/100px180" /> */}
  <Card.Body className="text-center" style={{color:"yellow",backgroundColor:"red"}}>
    <Card.Title>DEATH</Card.Title> 
    <h3>{this.state.data.deaths}</h3>

    <Card.Text>
    
    </Card.Text>
    {/* <Button variant="primary">Go somewhere</Button> */}
  </Card.Body>
</Card>

                    </div>


                </div>

            </div>
                {/* <p>india component</p> */}
                <div className="col-md-12">
                <State />
                </div>

                
            </div>
        );
    }
}

export default India;